.video-description {
  cursor: pointer;
  word-wrap: break-word;
}

.youtube-detail {
  border: 2px solid hsla(0, 100%, 85%, 0.5) !important;
}

.vimeo-detail {
  border: 2px solid hsla(200, 100%, 85%, 0.5) !important;
}

.not-found {
  margin-top: 10px !important;
}

.not-found div {
  margin-top: 4px !important;
  text-transform: none !important;
}
